  justify-content: space-between;
}
#search {
  display: flex;
  color: white;
  padding: 0px 0px 0px 10px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 40px;
}
body {
  background-color: black;
  color: white;
}
#input {
  color: black;
  padding: 10px;
}
.main-text {
  margin: 70px;
  justify-content: space-between;
