@import "tailwindcss";
#search-bar{
  margin-top: 10px;
  margin-left: 10px;
padding: 40px;
background-color: rgb(255, 255, 255);
justify-content: space-between;
}
#search {
  display: flex;
  
  color: white;
  padding: 0px 0px 0px 10px;
  margin-top: 10px;
  margin-left: 30px;
  margin-right: 40px;
  
}

#input{
  color: rgb(255, 255, 255);
  padding: 10px;
}


.nav-item{
  margin: 30px 50px 50px 50px ;
  display: flex;
  justify-content: space-between;
}
#hero-button{
  margin: 10px;
  color: white;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(249, 71, 0, 0.675);
  background-image: linear-gradient(to top,#2d970d 0%,#ffffff 100%);
  background-size:100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-decoration-color: transparent;

  



   
    

}
body{
  background-color: rgb(255, 255, 255);
  color:white
}
* {
    box-sizing: border-box;
    padding:0;
    margin: 0;
    font-family:'kum';
}
.navbar
{
    background: #131313;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top :0;
    z-index :999;


}
.navbar__container
{
  display: flex;
  justify-content: space-between;
  height: 100px;
  z-index: 1;
  width:100%;
  max-width: 1300px;
  margin:0 auto ;
  padding :0 50px;
}

#navbar__logo{
    background-color: red;
    background-image: linear-gradient(to top,#ff0844 0%,#ffb199 100%);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    display:  flex;
    text-decoration: none;
    font-size: 2rem;
    align-items: center;
}
.fa-gem{
    margin-right: 0.5rem;
}
.navbar__menu{
    display: flex;
    align-items:center;
    
    list-style:none;
    text-align:center;


}
.navbar__item{
    height: 80px;
}
.navbar__links{
    color:#fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding:0 1rem;
    height: 100%;
    
}
.navbar_btn{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    width :100%;
}
.button
{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding:10px;
    height: 100%;
    width:100%;
    border:none;
    outline: none;
    border-radius: 4px;
    background-color: #f77062;
    color: white;
}
.button:hover{
    background-color: #ff0844;
    transition:all 0.3s ease;

}
.navbar__links:hover{
    color: red;
    transition:all 0.3s ease;

}
@media screen and (max-width:960px){
    .navbar__container{
        display: flex;
        justify-content: space-between;
        height: 80px;
        z-index:1;
        width:100%;
        max-width:1300px;
        padding :0;

    }
    .navbar__menu{
        display: grid;
        grid-template-columns: auto;
        margin:0;
        width:100%;
        position:absolute;
        top: -1000px;
        opacity: 0%;
        transition: all 0.5s ease;
        height: 50vh;
        background: #131313;
        z-index:-1;

    }
    .navbar__menu.active{
       
        top:100%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 99;
        height: 50vh;
        font-size: 1.6rem;


    }
    #navbar__logo{
        padding-left:  25px;

    }
    .navbar__toggle .bar{
        width:25px;
        height: 3px;
        margin:5px auto;
        transition:all 0.3s ease-in-out;
        background: #fff;

    }
    .navbar__item{
        width:100%;
    }
    .navbar__links{
        text-align: center;
        padding: 2rem;
        width:100%;
        display: table;

    }
    #mobile-menu{
        position: absolute;
        top:20%;
        right: 5%;
        transform: translate(5%,10%);

    }
    .navbar_btn{
        padding-bottom: 2px;
    }
    .button{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80%;
        height: 80%;
        margin: 0%;

    }
    .navbar__toggle .bar{
        display: flex;
        cursor: pointer;
    }
    #mobile-menu.is-active .bar:nth-child(2){
        opacity: 0;
    }
    #mobile-menu.is-active .bar:nth-child(1){
        transform: translateY(8px) rotate(45deg);
    }
    #mobile-menu.is-active .bar:nth-child(3){
        transform: translateY(-8px) rotate(-45deg);
    }
    

}
